body{
    background: rgba(229, 229, 229, 1);
    font: "Century Gothic", Futura, sans-serif;
}
.menu{
    width: auto;
    min-width: 50px;
    height: auto;
    min-height: 30px;
    text-align: center;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: '10px';
    grid-auto-rows: minmax(100px, auto);
}
.submit{
    width: 80%;
    height: auto;
    min-height: 30px;
    text-align: center;
    margin: 0 auto;
    border-bottom: 2px solid rgb(100, 100, 100) ;
}

.items{
    width: auto;
    min-width: 15px;
    height: auto;
    min-height: 5px;
    text-align: center;
    margin: 0 auto;
    
}

.item{
    width: auto;
    min-width: 30px;
    height: auto;
    min-height: 10px;
    text-align: center;
    margin: 0 auto;
    background: rgba(208, 208, 208, 1);
}

.send{
    float: right;
    width: auto;
    max-width: 25px;
    height: auto;
    max-height: 25px;
}


@media only screen and (max-width: 900px) {
    body {
      background-color: lightblue;
      font: medium "Roboto", "Futura", sans-serif ;
    }
    .menu{
        width: auto;
        min-width: 50px;
        height: auto;
        min-height: 30px;
        text-align: center;
        margin: 0 auto;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: '10px';
        grid-auto-rows: minmax(100px, auto);
    }
    .submit{
        width: 80%;
        height: auto;
        min-height: 30px;
        text-align: center;
        margin: 0 auto;
        border-bottom: 2px solid rgb(100, 100, 100) ;
        background-color: white;
    }
    .items{
        width: auto;
        min-width: 15px;
        height: auto;
        min-height: 5px;
        text-align: center;
        margin: 0 auto;
        
    }
    
    .item{
        width: auto;
        min-width: 30px;
        height: auto;
        min-height: 10px;
        text-align: center;
        margin: 0 auto;
        background: rgb(202, 202, 245);
    }

    .send{
        float: right;
        width: auto;
        max-width: 20px;
        height: auto;
        max-height: 20px;
    }
  }